@charset "UTF-8";
/*
* FLOCSSベース 独自拡張
* https://github.com/hiloki/flocss
* core mixin/function/variable
* fundation normalize/reset/font/iconfont/
* layout header/footer/wrapper/main
* object component/project/utility
* page
* vendor
*/
// ---------------------------------------------------------------
// core - variable
// ---------------------------------------------------------------
@import "core/variables";
// ---------------------------------------------------------------
// core - function
// ---------------------------------------------------------------

// ---------------------------------------------------------------1
// core - mixin
// ---------------------------------------------------------------
@import "core/mixins/_arrow.scss";
@import "core/mixins/_button-3d-color.scss";
@import "core/mixins/_button-border-color.scss";
@import "core/mixins/_button-flat-color.scss";
@import "core/mixins/_button-ghost-color.scss";
@import "core/mixins/_button-size.scss";
@import "core/mixins/_circle.scss";
@import "core/mixins/_clearfix.scss";
@import "core/mixins/_font-line.scss";
@import "core/mixins/_grid.scss";
@import "core/mixins/_line-clamp.scss";
@import "core/mixins/_link-color.scss";
@import "core/mixins/_media.scss";
@import "core/mixins/_middle-line.scss";
@import "core/mixins/_opacity.scss";
@import "core/mixins/_position.scss";
@import "core/mixins/_rounded.scss";
.page-school,
.page-estate {
  #content {
  }

  .tb_search {
    th {
      width: 185px;
    }
  }

  // 物件検索ページ -----------------------------------------
  #search {
    .header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 860px;
      margin: 0 auto 40px;
      .title {
        margin: 0 auto;
        padding: 0;
        &:before,
        &:after {
          display: none;
        }
      }
      .topLink {
        padding-left: 40px;
        border-left: 2px solid #6f5421;
      }
    }
    .topLink {
      flex: 0 0 610px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0;
      padding-left: 0;
      text-align: center;
      li {
        flex: 0 0 48%;
        a {
          width: 100%;
        }
      }
    }
  }

  // 検索結果一覧ページ -----------------------------------------
  .result {
    &__content {
      border: none;
      width: 100%;
    }
    &__lead {
      background-color: #fff;
      border-bottom: 4px solid #000;
      margin: 80px 0 0;
      padding: 0 0 20px;
      width: 100%;
      font-weight: 700;
      @include font-line(25, 25);
      color: $color-text01;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &__paging {
      width :100%;
      margin: 20px auto;
      padding: 0;
      .page {
        margin: 0 0 15px;
      }
      .summary {
        &__total {
          margin-right: 5px;
          font-weight: 700;
          @include font-line(20, 20);
          color: #111;
          strong {
            margin-right: 10px;
            font-weight: 700;
            @include font-line(40, 40);
            color: $color-text01;
            display: inline-block;
          }
        }
        &__current {
          font-weight: 500;
          @include font-line(16, 16);
        }
      }
      .paging__left {
        margin: 0 0 20px;
        .bukken_total {
          margin-right: 20px;
          font-weight: 700;
          @include font-line(20, 20);
          strong {
            margin-right: 10px;
            font-weight: 700;
            @include font-line(50, 50);
            display: inline-block;
          }
        }
        .bukken_current {
          font-weight: 500;
          @include font-line(16, 16);
        }
      }
    }
    &__body {
      width: 100%;
      margin: 0 auto;
      padding: 0;
    }
    &__sort {
      width: 100%;
      height: 57px;
      margin-bottom: 15px;
      padding-bottom: 20px;
      ul {
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: flex-start;
        align-items: center;
      }
      li {
        margin-right: 10px;
        height: 37px;
        @include font-line(16, 16);
        display: flex;
        align-items: center;
        .sortSelect {
          width: 180px;
          height: 40px;
        }
        .button {
          width: 100px;
          height: 30px;
          @include font-line(14, 14);
          font-weight: 500;
          display: flex;
          justify-content: center;
          align-items: center;
          &.sort {
            background-color: #616161;
            color: #fff;
          }
          &.reset {
            background-color: #fff;
            color: $color-text01;
          }
          &:hover {
            opacity: 0.7;
          }
        }
      }
    }

    /*リスト表示部分*/
    .bukken {
      padding: 0;
      &__top {
        display: flex;
        margin-bottom: 14px;
        .button {
          cursor: pointer;
          width: 260px;
          position: relative;
          margin: 0;
          i {
            @include font-line(17);
          }
        }
        .check_all {
          margin-right: 10px;
          @include font-line(14, 40);
          font-weight: 700;
          letter-spacing: 0.04em;
          text-align: center;
          height: 40px;
          position: relative;
          cursor: pointer;
          input {
            vertical-align: middle;
          }
          .title {
          }
        }
        .go_inquiry_text {
          @include font-line(13, 40);
          font-weight: 700;
          margin: 0 10px 0 50px;
        }
      }
      &__item {
        border: 1px solid #000;
        margin-bottom: 30px;
        display: flex;
        position: relative;
        .left {
          background: #e2e2e2;
          flex: 0 0 60px;
          text-align: center;
          .label_checkbox {
            background: $color-accent;
            color: #fff;
            height: 45px;
            margin-bottom: 25px;
            font-weight: 500;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            &.open {
              @include font-line(12, 12);
              letter-spacing: -0.06em;
            }
            &.member {
              @include font-line(12, 16);
              padding-top: 5px;
            }
            &::after {
              width: 0;
              height: 0;
              border-left: 30px inset transparent;
              border-right: 30px inset transparent;
              border-top: 20px solid $color-accent;
              position: absolute;
              content: "";
              bottom: -20px;
              right: 0;
              z-index: 2;
            }
          }
          .parts {
            &::after {
              @include font-line(16);
              left: 3px;
              top: -14px;
            }
            &::before {
              background-color: #fff;
              border-color: #000;
              width: 17px;
              height: 17px;
              left: 3px;
              top: -15px;
            }
          }
          .member_icon {
            @include font-line(24, 24);
            position: relative;
            top: 6px;
          }
        }
        .right {
          flex: 1 1 100%;
          padding: 0;
          &__heading {
            background-color: #fff;
            margin: 0;
            padding: 20px 15px 0;
            .seal {
              margin: 0 0 5px;
              .type,
              .new,
              .pricedown {
                margin: 0 10px 5px 0;
              }
              .type {
                background-color: transparent;
              }
              .new {
                background-color: $color-sub;
                border: none;
                width: 90px;
                color: #fff;
              }
              .pricedown {
                background-color: $color-main;
                border: none;
                width: 90px;
                color: #fff;
              }
              @include clearfix();
            }
            .title {
              margin: 0;
              line-height: 25px;
              a {
                border: none;
                font-weight: 700;
                @include font-line(25, 33);
                color: $color-text01;
                text-decoration: none;
              }
            }
          }
          &__body {
            border: none;
            padding: 25px 15px 20px;
            color: $color-text01;
            text-decoration: none;
            display: flex;
            position: relative;
            &:hover {
              opacity: 0.65;
            }
            .photo {
              flex: 0 0 260px;
              height: 195px;
              position: relative;
              img {
                height: 100%;
                width: 100%;
                object-fit: contain;
                font-family: 'object-fit: contain;';
              }
              .stamp {
                width: 116px;
                height: 116px;
                border-radius: 50%;
                background-color: #000;
                color: #fff;
                @include font-line(16, 18);
                @include opacity(0.6);
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                margin: auto;
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                z-index: 2;
              }
            }
            .information {
              flex: 1 1 305px;
              margin-left: 20px;
              position: relative;
            }
          }
        }
        .groupButton {
          width: 280px;
          height: 35px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          position: absolute;
          top: 10px;
          right: 5px;
          .button {
            width: 130px;
            height: 35px;
            @include font-line(14, 14);
            font-weight: 700;
            border-radius: 18px;
            i {
              margin-right: 10px;
              @include font-line(16, 14);
            }
            &::after {
              display: none;
            }
          }
        }
      }
      &__private {
        margin: 0;
        padding: 0;
        position: relative;
        overflow: hidden;
        .ribbon {
          top: 5px;
        }
        a {
          background: url("/res/img/bg_estate_gentei_01.jpg") repeat top left;
          border-top: 4px solid $color-secondary;
          margin-bottom: 30px;
          color: #FFF;
          display: block;
          height: 270px;
          transition: all 0.4s ease;
          text-decoration: none;
          &:hover {
            text-decoration: none !important;
          }
          .buttonBlock {
            text-align: center;
            margin-top: 135px;
            .button {
              width: 360px;
            }
          }
        }
      }
    }

  }

  // 物件詳細ページ -----------------------------------------
  .detail {
    $detail: ".detail";
    position: relative;
    width: 700px;
		a.help{
      width: 320px;
      height: 50px;
      border-radius: 25px;
      border-width: 2px;
      margin: 30px 0 0;
      @include font-line(16, 16);
      i {
        margin-right: 10px;
      }
      &:hover {
        @include opacity(1);
        background-color: #fff;
        color: $color-accent;
      }
		}
    .buttonBlock {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .button {
        width: 340px;
        height: 50px;
        border-radius: 25px;
        margin: 0;
        @include font-line(16, 16);
        i {
          padding-right: 10px;
        }
        &::after {
          display: none;
        }
      }
    }
    .map-out {
      width: 100%;
      margin: 0 0 20px;
      #map-canvas {
        width: 100%;
        height: 400px;
        margin: 0 auto;
      }
    }
    #{$detail}__header {
      border-bottom: 4px solid #494440;
      margin: 0;
      padding: 0 0 10px;
      display: flex;
      &__left {
        .stamp {
          background-color: $color-sub;
          width: 61px;
          height: 61px;
          border-radius: 50%;
          margin-right: 10px;
          font-weight: 700;
          @include font-line(14, 14);
          color: #fff;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
      &__right {
        .stamps {
          @include clearfix();
          margin: 0;
          .pricedown,
          .new {
            border: none;
            width: 90px;
            color: #fff;
          }
          .pricedown {
            background-color: $color-main;
          }
          .new {
            background-color: $color-sub;
          }
        }
        .title {
          font-weight: 700;
          @include font-line(25, 38);
          margin: 0;
          padding: 0;
        }
      }
    }
    #{$detail}__body {
      padding: 15px 0 0;
    }
    #{$detail}__intro {
      margin-bottom: 20px;
    }
    #{$detail}__status {
      display: flex;
      .features {
        .feature {
          background-color: #eaeaea;
          width: 100px;
          height: 25px;
          margin: 0 10px 5px 0;
          @include font-line(13, 13);
          display: inline-flex;
          justify-content: center;
          align-items: center;
        }
      }
    }
    #{$detail}__description {
      margin: 20px 0;
      @include font-line(16, 24);
    }
    #{$detail}__section {
      margin: 40px 0 0;
      &__heading {
        margin: 0 0 20px;
      }
    }
    .gallery {
      $gallery: '.gallery';
      margin: 0;
      display: flex;
      flex-flow: row wrap;
      &__item {
        width: 160px;
        margin: 0 20px 20px 0;
        &:hover {
          #{$gallery}__photo {
            &::before {
              background: rgba(0,0,0,0.8);
              content: "";
              position: absolute;
              top: 0;
              bottom: 0;
              right: 0;
              left: 0;
              z-index: 1;
            }
            .zoomIcon {
              display: block;
            }
          }
        }
        &:nth-child(1),
        &:nth-child(2) {
          width: 340px;
          margin: 0 20px 20px 0;
          #{$gallery}__photo {
            height: 225px;
          }
        }
        &:nth-child(4n-2) {
          margin-right: 0;
        }
      }
      &__big {
        width: 340px;
        margin: 0 20px 20px 0;
        #{$gallery}__photo {
          height: 225px;
        }
        &:nth-child(even) {
          margin-right: 0;
        }
      }
      &__photo {
        border: 1px solid #ccc;
        display: block;
        height: 108px;
        width: 100%;
        position: relative;
        img {
          height: 100%;
          width: 100%;
          object-fit: contain;
          font-family: 'object-fit: contain;';
        }
        .zoomIcon {
          @include font-line(20, 20);
          display: none;
          color: #fff;
          height: 20px;
          width: 20px;
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto;
          z-index: 2;
        }
      }
      .kind {
        text-align: center;
      }
    }
  }
}
